
.shopcart {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 48px;
  z-index: 50;
}
.shopcart .content {
  display: -ms-flexbox;
  display: flex;
  height: 48px;
  background: #141d27;
  font-size: 0;
}
.shopcart .content .content-left {
  -ms-flex: 1;
      flex: 1;
}
.shopcart .content .content-left .logo-wrapper {
  display: inline-block;
  vertical-align: top;
  position: relative;
  top: -10px;
  margin: 0 12px;
  padding: 6px;
  width: 56px;
  height: 56px;
  box-sizing: border-box;
  border-radius: 50%;
  background: #141d27;
}
.shopcart .content .content-left .logo-wrapper .num {
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  border-radius: 16px;
  font-size: 8px;
  font-weight: 700;
  color: #fff;
  background: #f01414;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
}
.shopcart .content .content-left .logo-wrapper .logo {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #2b343c;
  text-align: center;
}
.shopcart .content .content-left .logo-wrapper .logo.active {
  background: #00a0dc;
}
.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart {
  line-height: 44px;
  font-size: 24px;
  color: #80858a;
}
.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart.active {
  color: #fff;
}
.shopcart .content .content-left .price {
  display: inline-block;
  vertical-align: top;
  margin-top: 12px;
  line-height: 24px;
  padding-right: 12px;
  box-sizing: border-box;
  border-right: 1px solid rgba(255,255,255,0.1);
  font-size: 16px;
  font-weight: 700;
  color: rgba(255,255,255,0.4);
}
.shopcart .content .content-left .price.active {
  color: #fff;
}
.shopcart .content .content-left .description {
  display: inline-block;
  vertical-align: top;
  margin: 12px 0 0 12px;
  line-height: 26px;
  color: rgba(255,255,255,0.4);
  font-size: 10px;
}
.shopcart .content .content-right {
  -ms-flex: 0 0 105px;
      flex: 0 0 105px;
  width: 105px;
}
.shopcart .content .content-right .pay {
  line-height: 48px;
  height: 48px;
  text-align: center;
  font-size: 12px;
  color: rgba(255,255,255,0.4);
  font-weight: 700;
  background: #2b333b;
}
.shopcart .content .content-right .pay.active {
  background: #00b43c;
  color: #fff;
}
.shopcart .ball-container .ball {
  position: fixed;
  bottom: 22px;
  left: 32px;
  z-index: 150;
  transition: all 0.4s cubic-bezier(0.34, -0.56, 0.73, 0.62);
}
.shopcart .ball-container .ball .inner {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #00a0dc;
  transition: all 0.4s linear;
}
.shopcart .shopcart-list {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  transition: all 0.5s;
  transform: translate3d(0, -100%, 0);
}
.shopcart .shopcart-list.fold-enter,
.shopcart .shopcart-list.fold-leave-active {
  transform: translate3d(0, 0, 0);
}
.shopcart .shopcart-list .list-header {
  height: 40px;
  line-height: 40px;
  padding: 0 18px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  background: #f3f5f7;
}
.shopcart .shopcart-list .list-header .title {
  float: left;
  font-size: 14px;
  color: #07111b;
}
.shopcart .shopcart-list .list-header .empty {
  float: right;
  font-size: 12px;
  color: #00a0dc;
}
.shopcart .shopcart-list .list-content {
  -ms-touch-action: none;
      touch-action: none;
  padding: 0 18px;
  max-height: 217px;
  overflow: hidden;
  background: #fff;
}
.shopcart .shopcart-list .list-content .food {
  position: relative;
  padding: 12px 0;
  box-sizing: border-box;
  position: relative;
}
.shopcart .shopcart-list .list-content .food::after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.shopcart .shopcart-list .list-content .food .name {
  line-height: 24px;
  font-size: 12px;
  color: #07111b;
}
.shopcart .shopcart-list .list-content .food .price {
  position: absolute;
  right: 90px;
  bottom: 12px;
  line-height: 24px;
  font-size: 14px;
  font-weight: 700;
  color: #f01414;
}
.shopcart .shopcart-list .list-content .food .cartcontrol-wrapper {
  position: absolute;
  right: 0;
  bottom: 6px;
}
.list-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 40;
  blur: 10px;
  transition: all 0.5s;
  opacity: 1;
  background: rgba(7,17,27,0.6);
}
.list-mask.fade-enter,
.list-mask.fade-leave-active {
  opacity: 0;
  background: rgba(7,17,27,0);
}


